function getComplaint() {
    return async (dispatch, getState) => {
        try {
            dispatch(update({
                complaintLoading: true,
            }));
            const dataContext = getState().homepage.testViewModel;
            let res = [
                // {name: "111",remarks: "11khfkjdshfkjash" ,days: 10, times: 12, num: 13, tips: "khfkjdshfkjashjsfkahjkfdshj", time: "12-22"},
                // {name: "221",remarks: "22khfkjdshfkjash" ,days: 10, times: 15, num: 13, tips: "khfkjdshfkjashjsfkahjkfdshj", time: "12-21"},
                // {name: "1331",remarks: "333khfkjdshfkjash" ,days: 32, times: 12, num: 13, tips: "khfkjdshfkjashjsfkahjkfdshj", time: "12-22"},
                // {name: "1331",remarks: "333khfkjdshfkjash" ,days: 32, times: 12, num: 13, tips: "khfkjdshfkjashjsfkahjkfdshj", time: "12-22"}
            ];
            res = _.map(res, (item, index) => {
                const dataArr = [item.days, item.times, item.num];
                const max = _.max(dataArr);
                return {
                    ...item,
                    max
                };
            });

            dispatch(update({
                complaintData: res,
            }));
        } finally {
            dispatch(update({
                complaintLoading: false
            }));
        }
    }
}


export default {
    update,
    getAddAndSolve,
    getUnsolve,
    getProduct,
    getProjectDatas,
    getProject,
    getComplaint,

}



